import { RendererCanvas, getImgFromURL } from '../../../lib';

import { loadSVGFromHTTP } from '../../../lib/njscore-package/src';

import { CFilter } from './CFilter';
export enum ColorCorrection {
    AutoContrast = 'autoContrast',
    None = 'None'
};
export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;

    constructor(id: string) {
        this.divHost = document.getElementById(id)

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';
        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#555';
        div.style.margin = '50px';
        div.style.background = 'rgba(0,0,0,0)';
        this.divHost.appendChild(div);

        this.renderer = new Renderer(div);
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
};
export class Renderer extends RendererCanvas {
    img: HTMLImageElement | undefined = undefined;
    rotation = 0.0;
    m: number[] = [0, 0];

    switch = true;
    isDithering = false;
    typeCorrection: ColorCorrection = ColorCorrection.AutoContrast;

    params = {
        "blur": 0,
        "sepia": 0,
        "contrast": 1,
        "brightness": 1,
        "saturate": 1,
        "hueRotate": 0,
    };
    public cFilter: CFilter;

    constructor(div: any) {
        super(div);
        this.ctx = this.canvas.getContext('2d', { willReadFrequently: true }) as CanvasRenderingContext2D;

        this.isStatic = true;

        this.canvas.style.position = 'relative';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';


        let imgSVG = new Image(20, 20);
        imgSVG.src = process.env.PUBLIC_URL + '/static/svg/moon.svg'; // d as string;
        div.appendChild(imgSVG);

        getImgFromURL('https://www.glenstone.org/wp-content/uploads/2018/07/AV_Landscape-Hero-Contour-2993-1276x800.jpg').then((img: HTMLImageElement) => {
            // getImgFromURL('https://upload.wikimedia.org/wikipedia/commons/f/ff/Mozilla_Firefox_logo_2013.png').then((img: HTMLImageElement) => {
            this.img = img;
            this.img.onload = (ev) => {
                this.update(img);
            };
        });

        const divInput = document.createElement('input');
        divInput.value = 'https://upload.wikimedia.org/wikipedia/commons/f/ff/Mozilla_Firefox_logo_2013.png';
        divInput.addEventListener('keydown', (e: KeyboardEvent) => {
            if (e.code === 'Enter') {
                getImgFromURL(divInput.value).then((img: HTMLImageElement) => {
                    this.img = img;
                    this.img.onload = (ev) => {
                        this.update(img);
                    };
                });
            }
        });
        this.divHost.appendChild(divInput);


        const callBack = (img) => {
            this.img = img;
            this.img.onload = (ev) => {
                this.update(img);
            };
        };
        const divFileInput = getImageUploader(callBack);
        this.divHost.appendChild(divFileInput);

        const slType = getSlider('saturate', 0, 200)
        slType.slider.defaultValue = '100';
        this.divHost.appendChild(slType.div);
        slType.slider.oninput = () => {
            const value = parseInt(slType.slider.value) * 0.01;
            // console.log(value);
            this.params.saturate = value;
            this.render(this.ctx);
        };
        const slType2 = getSlider('hueRotate', -360, 360)
        slType2.slider.defaultValue = '0';
        this.divHost.appendChild(slType2.div);
        slType2.slider.oninput = () => {
            const value = parseInt(slType2.slider.value) * 1;
            // console.log(value);
            this.params.hueRotate = value;
            this.render(this.ctx);
        };
        const slType3 = getSlider('contrast', 0, 20)
        slType3.slider.defaultValue = '10';
        this.divHost.appendChild(slType3.div);
        slType3.slider.oninput = () => {
            const value = parseInt(slType3.slider.value) * .1;
            // console.log(value);
            this.params.contrast = value;
            this.render(this.ctx);
        };
        const slType4 = getSlider('brightness', 0, 20)
        slType4.slider.defaultValue = '10';
        this.divHost.appendChild(slType4.div);
        slType4.slider.oninput = () => {
            const value = parseInt(slType4.slider.value) * .1;
            // console.log(value);
            this.params.brightness = value;
            this.render(this.ctx);
        };
        const slType5 = getSlider('sepia', 0, 100)
        slType5.slider.defaultValue = '0';
        this.divHost.appendChild(slType5.div);
        slType5.slider.oninput = () => {
            const value = parseInt(slType5.slider.value) * .01;
            // console.log(value);
            this.params.sepia = value;
            this.render(this.ctx);
        };

        const reset = document.createElement('button');
        this.divHost.appendChild(reset);
        reset.style.width = '50px';
        reset.style.height = '50px';
        reset.textContent = 'Reset';
        reset.onclick = () => {
            this.resetParam();
            this.render(this.ctx);
        }

        const preset = document.createElement('button');
        this.divHost.appendChild(preset);
        preset.style.width = '50px';
        preset.style.height = '50px';
        preset.textContent = 'preset';
        preset.onclick = () => {
            this.renderWithPreset();
            this.render(this.ctx);
        }

        this.start();
    }
    public update(img: HTMLImageElement) {
        this.img = img;
        this.canvas.width = this.img.width;
        this.canvas.height = this.img.height;

        if (this.cFilter) { this.cFilter.destroy() }
        this.cFilter = new CFilter(this.img);

        this.render(this.ctx);
    }
    public render(ctx: CanvasRenderingContext2D): void {
        if (this.img === undefined || this.cFilter === undefined) { return; }

        const params = this.params;
        this.cFilter.applyFilter(ctx, params)

    }
    public renderWithPreset(preset: string = 'inkwell') {
        this.resetParam();
        for (let i in PRESET[preset]) {
            this.params[i] = PRESET[preset][i];
        }
        // console.log('done');        
    }
    public resetParam() {
        this.params = {
            "blur": 0,
            "sepia": 0,
            "contrast": 1,
            "brightness": 1,
            "saturate": 1,
            "hueRotate": 0,
        };
    }
    public destroyPost() {
        this.cFilter.destroy();
    }
};

export const PRESET_TYPE = {
    aden: 'aden',
    poprocket: 'poprocket',
    inkwell: 'inkwell',

}

export const PRESET = {
    aden: {
        sepia: .35,
        contrast: 1.1,
        brightness: 1.2
    },
    poprocket: {
        sepia: .15,
        brightness: 1.2
    },
    inkwell: {
        saturate: 0,
        contrast: 0.85,
        brightness: 1.25
    },
}

const getSlider = (title: string, min: number, max: number) => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = `${min}`;
    slider.max = `${max}`;

    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);
    return {
        div, slider, text
    }
};

const getImageUploader = (callBack: (img: HTMLImageElement) => void) => {
    const divInputFile = document.createElement('input');
    divInputFile.type = 'file';
    divInputFile.addEventListener('change', (event) => { 
       // @ts-ignore
        const file = event.target.files[0];
        if (file) {
            const reader = new FileReader();
            reader.onload = (e) => {
                const img = new Image();
                img.src = e.target.result as string;
                callBack(img)      
            }
            reader.readAsDataURL(file);
        }
    });
    return divInputFile
};