
class CoreWebGL2 {
    private positionBuffer: WebGLBuffer;
    private texCoordBuffer: WebGLBuffer;
    private vertexShader: WebGLShader;
    private fragmentShader: WebGLShader;
    private texture: WebGLTexture;
    private canvasGL: HTMLCanvasElement;
    private img: HTMLImageElement;
    public program: WebGLProgram;
    public gl: WebGLRenderingContext;
    constructor(img: HTMLImageElement, vertexShaderSource: string, fragmentShaderSource: string) {
        this.canvasGL = document.createElement('canvas');
        this.canvasGL.width = img.width;
        this.canvasGL.height = img.height;
        this.gl = this.canvasGL.getContext('webgl2');
        this.img = img;
        this.init(vertexShaderSource, fragmentShaderSource )
    }
    public get canvas() {
        return this.canvasGL;
    }
    private init(vertexShaderSource: string, fragmentShaderSource: string) {
        const gl = this.gl;
        this.texture = gl.createTexture();
        gl.bindTexture(gl.TEXTURE_2D, this.texture);
        gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA, gl.RGBA, gl.UNSIGNED_BYTE, this.img);
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.CLAMP_TO_EDGE);
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.CLAMP_TO_EDGE);
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.LINEAR);
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.LINEAR);

        this.vertexShader = gl.createShader(gl.VERTEX_SHADER);
        gl.shaderSource(this.vertexShader, vertexShaderSource);
        gl.compileShader(this.vertexShader);

        this.fragmentShader = gl.createShader(gl.FRAGMENT_SHADER);
        gl.shaderSource(this.fragmentShader, fragmentShaderSource);
        gl.compileShader(this.fragmentShader);

        this.program = gl.createProgram();
        gl.attachShader(this.program, this.vertexShader);
        gl.attachShader(this.program, this.fragmentShader);
        gl.linkProgram(this.program);
        gl.useProgram(this.program);

        const positionAttributeLocation: number = gl.getAttribLocation(this.program, 'a_position');
        const texCoordAttributeLocation: number = gl.getAttribLocation(this.program, 'a_texCoord');
        const textureUniformLocation: WebGLUniformLocation = gl.getUniformLocation(this.program, 'u_texture');
        gl.uniform1i(textureUniformLocation, 0);

        this.positionBuffer = gl.createBuffer();
        gl.bindBuffer(gl.ARRAY_BUFFER, this.positionBuffer);
        gl.bufferData(gl.ARRAY_BUFFER, new Float32Array([-1, -1, 1, -1, -1, 1, 1, 1]), gl.STATIC_DRAW);
        gl.enableVertexAttribArray(positionAttributeLocation);
        gl.vertexAttribPointer(positionAttributeLocation, 2, gl.FLOAT, false, 0, 0);

        this.texCoordBuffer = gl.createBuffer();
        gl.bindBuffer(gl.ARRAY_BUFFER, this.texCoordBuffer);
        gl.bufferData(gl.ARRAY_BUFFER, new Float32Array([0, 0, 1, 0, 0, 1, 1, 1]), gl.STATIC_DRAW);
        gl.enableVertexAttribArray(texCoordAttributeLocation);
        gl.vertexAttribPointer(texCoordAttributeLocation, 2, gl.FLOAT, false, 0, 0);

        gl.activeTexture(gl.TEXTURE0);
        gl.bindTexture(gl.TEXTURE_2D, this.texture);
    }
    public destroy() {
        this.gl.deleteProgram(this.program);
        this.gl.deleteTexture(this.texture);
        this.gl.deleteShader(this.vertexShader);
        this.gl.deleteShader(this.fragmentShader);
        this.gl.deleteBuffer(this.positionBuffer);
        this.gl.deleteBuffer(this.texCoordBuffer);
        // console.log('removed');
    }
}

export class CFilter {
    private vertexShaderSource = `
        attribute vec2 a_position;
        attribute vec2 a_texCoord;
        varying vec2 v_texCoord;

        void main() {
            gl_Position = vec4(a_position, 0, 1);
            v_texCoord = a_texCoord;
        }
    `;

    private fragmentShaderSource = `
        precision mediump float;
        
        uniform vec2 u_resolution;
        uniform float u_blurRadius;
        uniform float u_sepia;
        uniform float u_contrast;
        uniform float u_brightness;
        uniform float u_saturation;
        uniform float u_hueRotation;
        uniform sampler2D u_texture;
        varying vec2 v_texCoord;

        vec4 blur5(sampler2D image, vec2 uv, vec2 resolution, vec2 direction) {
            vec4 color = vec4(0.0);
            vec2 off1 = vec2(1.3333333333333333) * direction;
            color += texture2D(image, uv) * 0.29411764705882354;
            color += texture2D(image, uv + (off1 / resolution)) * 0.35294117647058826;
            color += texture2D(image, uv - (off1 / resolution)) * 0.35294117647058826;
            return color; 
        }

        void main() {
            vec2 flippedTexCoord = vec2(v_texCoord.x, 1.0 - v_texCoord.y);
            vec4 color = texture2D(u_texture, flippedTexCoord);

            float gray = dot(color.rgb, vec3(0.299, 0.587, 0.114));

            // ..................
            if (u_sepia > .0) {
                vec3 sepia = vec3(1.0, 0.95, 0.82);
                color = mix(color, vec4(gray * sepia, color.a), u_sepia);
            }


            // ..................
            // if ((u_brightness != 1.0) || (u_contrast != 1.0)) {
                vec3 colorRgb = color.rgb * u_brightness;
                colorRgb = (colorRgb - vec3(0.5)) * max(u_contrast, 0.0) + vec3(0.5);
                color = vec4(colorRgb, color.a);
            // }


            // ..................
            color = vec4(mix(vec3(gray), color.rgb, u_saturation), color.a);

            
            // ..................
            if (u_hueRotation != .0){
                float angle = u_hueRotation * 3.14159265358979323846264 / 180.0;
                float s = sin(angle);
                float c = cos(angle);
                mat3 hueRotationMatrix = mat3(
                    0.213 + 0.787 * c - 0.213 * s, 0.715 - 0.715 * c - 0.715 * s, 0.072 - 0.072 * c + 0.928 * s,
                    0.213 - 0.213 * c + 0.143 * s, 0.715 + 0.285 * c + 0.140 * s, 0.072 - 0.072 * c - 0.283 * s,
                    0.213 - 0.213 * c - 0.787 * s, 0.715 - 0.715 * c + 0.715 * s, 0.072 + 0.928 * c + 0.072 * s
                );
                colorRgb = color.rgb * hueRotationMatrix;
                color = vec4(colorRgb, color.a);
            }

            // ..................
            gl_FragColor = color;
        }
    `;
    private img: HTMLImageElement;
    private params = {
        "blur": 0,
        "sepia": 0,
        "contrast": 1,
        "brightness": 1,
        "saturate": 1,
        "hueRotate": 0,
    };
    private core: CoreWebGL2;
    constructor(img: HTMLImageElement) {
        this.img = img;
        this.core = new CoreWebGL2(img, this.vertexShaderSource, this.fragmentShaderSource);
    }
    public applyFilter(ctx: CanvasRenderingContext2D, param: any) {
        this.params = param;
        const gl = this.core.gl;

        const sepiaUniformLocation = gl.getUniformLocation(this.core.program, 'u_sepia');
        gl.uniform1f(sepiaUniformLocation, this.params.sepia);
        const saturationUniformLocation = gl.getUniformLocation(this.core.program, 'u_saturation');
        gl.uniform1f(saturationUniformLocation, this.params.saturate);
        const brightnessUniformLocation = gl.getUniformLocation(this.core.program, 'u_brightness');
        gl.uniform1f(brightnessUniformLocation, this.params.brightness);
        const contrastUniformLocation = gl.getUniformLocation(this.core.program, 'u_contrast');
        gl.uniform1f(contrastUniformLocation, this.params.contrast);
        const hueRotationUniformLocation = gl.getUniformLocation(this.core.program, 'u_hueRotation');
        gl.uniform1f(hueRotationUniformLocation, this.params.hueRotate);
        const blurUniformLocation = gl.getUniformLocation(this.core.program, 'u_blurRadius');
        gl.uniform1f(blurUniformLocation, 1.8);
        const resolutionUniform = gl.getUniformLocation(this.core.program, 'u_resolution');
        gl.uniform2f(resolutionUniform, gl.canvas.width, gl.canvas.height);
        gl.drawArrays(gl.TRIANGLE_STRIP, 0, 4);

        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.drawImage(this.core.canvas, 0, 0);
    }
    public destroy() {
        this.core.destroy();
    }
}